.content h2 {
  margin-bottom: 30px;
}
.content .top {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  justify-content: space-between;
}
.content .top .cont {
  background-color: #fff;
  padding: 25px 30px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  width: 32%;
}
.content .top .cont .left {
  width: 60px;
  height: 60px;
  display: block;
  text-align: center;
  border-radius: 10px;
}
.content .top .cont .left i {
  line-height: 60px;
  font-size: 28px;
}
.content .top .cont .right {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 20px;
}
.content .top .cont .right .title {
  font-weight: 600;
  color: #a1a5b5;
  font-size: 13px;
  margin-bottom: 10px;
}
.content .top .cont .right .conf {
  font-size: 13px;
  font-weight: 900;
  color: #a1a5b5;
  margin-bottom: 2px;
}
.content .top .cont .right .bold {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 2px;
  color: #27323f;
}
.content .tasks_manager {
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
  background-color: #fff;
  margin-top: 30px;
  padding: 30px;
}
.content .tasks_manager .title {
  font-weight: 600;
  display: block;
  font-size: 18px;
}
.content .tasks_manager > button {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #fff;
  background-color: #2269f5;
  border-color: #2269f5;
  padding: 6px 20px;
  border-radius: 5px;
  font-size: 14px;
  user-select: none;
  transition: all 0.2s ease-in-out !important;
  margin-top: 25px;
  margin-bottom: 25px;
}
.content .tasks_manager > button i {
  line-height: 21px;
  margin-left: 6px;
  margin-right: -8px;
  font-size: 20px;
}
.content .tasks_manager table {
  width: 100%;
  color: #212529;
  vertical-align: top;
  border-color: #dee2e6;
  border-collapse: collapse;
}
.content .tasks_manager table thead {
  border-style: solid;
  border-width: 0;
  border-color: #e1e7ed !important;
}
.content .tasks_manager table thead tr {
  border-style: solid;
  border-width: 0;
  border-color: #e1e7ed;
}
.content .tasks_manager table thead tr th {
  font-weight: 500;
  color: #7b8c9d;
  text-align: start;
  padding-bottom: 10px;
}
.content .tasks_manager table tbody {
  border-style: solid;
  border-width: 0;
  border-color: #e1e7ed !important;
}
.content .tasks_manager table tbody tr {
  background-color: #f4f7fa;
  border-top: 2px solid #dddddd5c;
}
.content .tasks_manager table tbody tr td.button {
  margin-left: -22px;
  display: block;
  padding: 5px 0;
}
.content .tasks_manager table tbody tr td {
  border-style: solid;
  border-width: 0;
  border-color: #e1e7ed !important;
  text-align: start;
  font-weight: 600;
  color: #27323f;
}
.content .tasks_manager table tbody tr td span:not(span.status) {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content .tasks_manager table tbody tr td span:not(span.status) button {
  cursor: pointer;
  padding: 3px 14px;
  border-radius: 5px;
  font-size: 14px;
  user-select: none;
  transition: all 0.2s ease-in-out !important;
  color: #fff;
  background-color: #ff4857;
  margin: 1px;
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  border: 1px solid transparent;
}
.content .tasks_manager table tbody tr td span:not(span.status) button i {
  vertical-align: text-top;
  line-height: 21px;
  margin-right: 6px;
  margin-left: -8px;
  font-size: 20px;
}
.content .box_new_task {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0px;
  right: 0px;
  background: #00000070;
  z-index: 3;
}
.content .box_new_task .add_task {
  position: fixed;
  display: flex;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
  flex-direction: column;
  padding: 20px;
  z-index: 3;
  width: 500px;
}
.content .box_new_task .add_task .t-c {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .box_new_task .add_task .t-c .title {
  font-size: 16px;
  color: #293442;
  margin-bottom: 10px;
  font-weight: 600;
}
.content .box_new_task .add_task .t-c .close-box {
  box-sizing: content-box;
  width: 1em;
  height: 1em;
  color: #000;
  background: transparent url(/static/img/close.svg) center/1em auto no-repeat;
  border: 0;
  border-radius: 0.25rem;
  opacity: 0.5;
  cursor: pointer;
}
.content .box_new_task .add_task .cont label {
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 500;
  margin-top: 15px;
}
.content .box_new_task .add_task .cont select,
.content .box_new_task .add_task .cont textarea,
.content .box_new_task .add_task .cont input {
  border-radius: 8px;
  padding: 10px 18px;
  color: #24292e;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  display: block;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  margin: 0;
  border: 0.5px solid #ced4da;
  outline: none;
}
.content .box_new_task .add_task .cont select:focus,
.content .box_new_task .add_task .cont textarea:focus,
.content .box_new_task .add_task .cont input:focus {
  border: 0.5px solid #128c7e;
}
.content .box_new_task .add_task .cont textarea {
  height: 100px;
  resize: none;
}
.content .box_new_task .add_task .cont .duble {
  display: flex;
  align-items: center;
}
.content .box_new_task .add_task .cont .duble > span {
  width: 50%;
  padding-right: 10px;
}
.content .box_new_task .add_task .cont .duble > span:first-of-type {
  padding-left: 10px;
  padding-right: 0;
}
.content .box_new_task .add_task .bottom {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  margin-top: 15px;
  justify-content: space-between;
}
.content .box_new_task .add_task .bottom input,
.content .box_new_task .add_task .bottom span,
.content .box_new_task .add_task .bottom .task_add {
  display: inline-block;
  padding: 8px 20px;
  border-radius: 5px;
  font-size: 14px;
  user-select: none;
  transition: all 0.2s ease-in-out !important;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  cursor: pointer;
  border: none;
}
.content .box_new_task .add_task .bottom input {
  background-color: #04b07d;
}
.content .box_new_task .add_task .bottom .task_add {
  background-color: #04b07d6b !important;
}
.content .box_new_task .add_task .bottom span {
  background-color: #ff4857;
}

@media (max-width: 950px) {
  .content .top {
    flex-direction: column !important;
  }
  .content .top .cont {
    width: 100% !important;
    margin-bottom: 15px !important;
  }
}
@media (max-width: 580px) {
  .add_task {
    max-width: 90% !important;
  }

  .content h2 {
    margin-bottom: 20px !important;
  }
  .content .tasks_manager {
    padding-left: 10px !important;
    padding-right: 10px !important;
    margin-top: 15px !important;
  }
  .content .tasks_manager table tbody tr td.button {
    margin-left: 0px !important;
  }
}
.task_available {
  background: #04b07d;
  color: white;
  padding: 2px 10px;
  font-size: 13px;
  border-radius: 3px;
}

.task_unavailable {
  background: #ff4857;
  color: white;
  padding: 2px 10px;
  font-size: 13px;
  border-radius: 3px;
}